import React from "react";
import { Card, CloseButton, Col, Row } from "react-bootstrap";
import Image from "./Image";

export default function CartItem({ item, remove }) {
  return (
    <Card className="cart-item">
      <Row className="align-items-center">
        <Col md={4}>
          <Image {...item.image} />
        </Col>
        <Col md={8}>
          <Card.Body>
            <Card.Title>{item.style.caption}</Card.Title>
            <Card.Text>
              :{" "}
              {item.film.properties
                .map((el) => `${el.name}, ${el.value}`)
                .join(", ")}
              <br />
              : {item.color.caption}
              <br />
                (): {item.width} x {item.height}  <br />
              {item.combinedHeight && (
                <>
                  <span>
                       : {item.combinedHeight} .
                  </span>{" "}
                  <br />
                </>
              )}
              -: {item.count} . <br />
              : {item.price}  <br />
              {item.discountValue > 0 && (
                <>
                  <span>: {item.discountValue} .</span>
                  <br />
                </>
              )}
              <b>: {item.total} .</b>
              <br />
            </Card.Text>
            <CloseButton
              type="button"
              className="cart-item-close"
              onClick={() => remove(item.id)}
            ></CloseButton>
          </Card.Body>
        </Col>
      </Row>
    </Card>
  );
}
